.auth__button{
  background-color: #fff;
  border: 0;
  outline: 0;
  margin-top: 186px;
  font-weight: 400;
  font-size: 18px;
  line-height: 22px;
  width: 358px;
  height: 50px;
  text-align: center;
  border-radius: 2px;
  cursor: pointer;
  transition: opacity .5s ease;
}

.auth__button:hover {
  opacity: .85;
}